import { NodeQue } from "./NodeQue";

/**
 * 2018329 
 * 
 */

export default class ToolBar{
    paper: RaphaelPaper
    option: rSu.bsMap
    ndMer: rSu.NodeQue
    // title node 
    protected tNodes: {
        [k: string]: RaphaelElement
    } = {}
    // 
    protected tBodyNds: rSu.Node[]

    // 
    protected cNodes:{
        [k: string]: RaphaelElement
    } = {}
    // 
    protected cBodyNds: rSu.Node[]


    protected _tools: RaphaelElement[] = []     // 
    constructor(paper: RaphaelPaper, opt?: rSu.bsMap){
        this.paper = paper
        this.ndMer = new NodeQue(this.paper)
        this.option = opt
        this._create()
    }
    /**
     * 
     */
    private _create(){
        // console.log(this.paper)
         // 
         var $tool: Dance.Tool = {},
            $this = this,
            // tNodes: RaphaelElement[] = []
            // 
            tNodes: {
                [k: string]: RaphaelElement
            } = {},
            tBodyNds: rSu.Node[] = []         // 

            // 
            , cNodes: {
                [k: string]: RaphaelElement
            } = {},
            cBodyNds: rSu.Node[] = []         // 

         var raphael = this.paper
         var ctX = 5, 
             ctY = 5,
             ctW = 75,
             ctH = 300,
             x = ctX, y = ctY,            // 
             config = this.option,
             pkgClr = config.pkgClr,
             ist: rSu.Node,
             ndMer = this.ndMer
         
         //             
         // var dragHandlerEvnt = function(){}
 
         // 
        //  $tool.containerIst = raphael.rect(ctX, ctY, ctW, ctH)
        //  $tool.containerIst.attr('fill', '#ffffff')      // 
        
        // data: toggle => H/S
        tNodes['title'] = raphael.rect(x, y, ctW, 23)
            .attr('fill', '#ffffff')
            .click(function(){
                // console.log(this)
                let toggle = this.data('toggle')
                let iconIst = tNodes['icon'],
                    tBody = tNodes['tBody']
                if(toggle != 'H'){
                    this.data('toggle', 'H')
                    if(iconIst){
                        iconIst.attr('src', './arrow_down.png')
                    }
                    if(tBody){
                        tBody.hide()
                    }
                    $this.tToggle('H')
                }else{
                    this.data('toggle', 'S')
                    if(iconIst){
                        iconIst.attr('src', './arrow_up.png')
                    }
                    if(tBody){
                        tBody.show()
                    }
                    $this.tToggle('S')
                }
            })
        tNodes['icon'] = raphael.image('./arrow_up.png', x+1, y+1, 20, 20)

        y += 23
        tNodes['tBody'] = raphael.rect(x, y, ctW, 250)
            .attr('fill', '#ffffff')
        
        // 
        x += 20, y += 50
        ist = ndMer.make('begin', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)

        // 
        y += 20
        ist = ndMer.make('task', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)
        
        // 
        y += 20
        ist = ndMer.make('audit', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)

        // 
        y += 20
        ist = ndMer.make('sign', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)
       
        // 
        y += 20
        ist = ndMer.make('cond', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)

        // 
        y += 20
        ist = ndMer.make('subFlow', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)

        // 
        y += 30
        ist = ndMer.make('parallel', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)
       
        // 
        y += 20
        ist = ndMer.make('merge', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)
      
        // 
        y += 20
        ist = ndMer.make('end', {cx: x, cy: y, w: 16, h: 12})
            .creator()
        tBodyNds.push(ist)

        this.tNodes = tNodes
        this.tBodyNds = tBodyNds

        // 
        
        // data: toggle => H/S
        x = x - 20,
        y += 10
        cNodes['title'] = raphael.rect(x, y, ctW, 23)
            .attr('fill', '#ffffff')
            .click(function(){
                // console.log(this)
                let toggle = this.data('toggle')
                let iconIst = cNodes['icon'],
                    cBody = cNodes['tBody']
                if(toggle != 'H'){
                    this.data('toggle', 'H')
                    if(iconIst){
                        iconIst.attr('src', './arrow_down.png')
                    }
                    if(cBody){
                        cBody.hide()
                    }
                    $this.cToggle('H')
                }else{
                    this.data('toggle', 'S')
                    if(iconIst){
                        iconIst.attr('src', './arrow_up.png')
                    }
                    if(cBody){
                        cBody.show()
                    }
                    $this.cToggle('S')
                }
            })
        cNodes['icon'] = raphael.image('./arrow_up.png', x+1, y+1, 20, 20)
        
        y += 23
        cNodes['tBody'] = raphael.rect(x, y, ctW, 80)
            .attr('fill', '#ffffff')
        
        x = x + 20
        // 
        y += 20
        ist = ndMer.make('ln', {
            P1: {x: x-5, y},
            P2: {x: x+10, y}
        })
            .creator()
        cBodyNds.push(ist)

        // 
        y += 20
        ist = ndMer.make('lnPoly', {
            P1: {x: x-5, y},            
            P2: {x: x+10, y: y + 4},
            h: 4
        })
            .creator()
        cBodyNds.push(ist)

        this.cNodes = cNodes
        this.cBodyNds = cBodyNds

    }
    /**
     * 
     * @param {string} type  H/S
     */
    tToggle(type?:string){
        var tBodyNds = this.tBodyNds
        if('H' != type){
            tBodyNds.forEach((nd) => {
                nd.show()
            })
        }
        else{
            tBodyNds.forEach((nd) => {
                nd.hide()
            })
        }
    }
    /**
     * 
     * @param {string} type  H/S
     */
    cToggle(type?:string){
        var cBodyNds = this.cBodyNds
        if('H' != type){
            cBodyNds.forEach((nd) => {
                nd.show()
            })
        }
        else{
            cBodyNds.forEach((nd) => {
                nd.hide()
            })
        }
    }

}


